export const nodeColors: Record<string, string> = {
  LANDSCAPE: "#28441E",
  OBJECTS: "#ED2024",
  PHOTOGRAPHY: "#F7EC13",
  MUSIC: "#3953A4",
  SCULPTURE: "#ED7823",
  PERFORMANCE: "#B34D9D",
  VIDEO: "#4D2A7C",
  WEARABLES: "#231F20v",
  DRAWINGS: "#ED1D7E",
  INSTALLATIONS: "#4CC5DC",
  COMISSIONS: "#911A1D",
  DIGITAL: "#000000",
  INTERACTIVE: "#C0C0C0",
};

export const getNodeColor = (tag?: string): string =>
  nodeColors[tag?.toUpperCase() ?? ""] || "#ffffff";
